<template>
  <el-submenu v-if="item.children" v-bind:index="item.path">
    <template slot="title">{{ item.name }}</template>
    <el-menu-item
                  v-for="(sonItem, sonIndex) in item.children"
                  :item="sonItem"
                  :key="sonIndex"
                  :index="sonItem.path">{{ sonItem.name }}</el-menu-item>
  </el-submenu>
  <el-menu-item v-else :index="item.path">
    {{ item.name }}
  </el-menu-item>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true
    }
  }
}
</script>



