<template>
  <div id="header-wrap">
    <div class="logo-wrap"></div>
    <div class="user-wrap">
      <div class="user-info">
        <el-dropdown @command="handleCommand">
        <span class="el-dropdown-link" style="color: #ffffff;">
          {{ userInfo.nickname }}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="personInfo">个人信息</el-dropdown-item>
            <el-dropdown-item command="logout">退出登陆</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="header-image">
        <img :src=userInfo.headerPic alt="">
      </div>
    </div>
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <menu-item v-for="(item, index) in routes" :item="item" :key="index" />
    </el-menu>
  </div>
</template>

<script>
import MenuItem from '@/components/layout/MenuItem'

export default {
  data () {
    return {
      activeIndex: '/',
      routes: [],
      userInfo: {
        headerPic: require('../../assets/header-pic.jpg'),
        nickname: '清流'
      }
    }
  },
  components: { MenuItem },
  created: function () {
    this.activeIndex = this.$route.path
    this.$router.options.routes.forEach(item => {
      let res = this.getChildrenRoutes(item, '')
      if (res) {
        this.routes.push(res)
      }
    })
  },
  methods: {
    handleSelect (key, keyPath) {
      if (keyPath.length === 1) {
        keyPath[1] = '/'
      }
      this.$router.push(keyPath[1])
    },
    getChildrenRoutes (children, pPath) {
      if (children.hidden !== true) {
        let one = {
          path: (pPath + '/' + children.path).replace(/\/\//g, '/'),
          name: children.name
        }
        if (children.children) {
          one.children = []
          children.children.forEach(item => {
            let res = this.getChildrenRoutes(item, one.path)
            res && one.children.push(res)
          })
          one.children.length === 0 && (delete one.children)
        }
        return one
      } else {
        return null
      }
    },
    handleCommand (command) {
      if (command === 'logout') {
        sessionStorage.removeItem('userInfo')
        this.$router.push('/login')
        // return
      }
    }
  }
}
</script>

<style scoped>
  #header-wrap {
    width: inherit;
    height: 60px;
    position:fixed;
    top:0;
    z-index: 333;
    background-color: rgb(84, 92, 100);
    min-width: 960px;
  }
  .el-menu-demo {
    float: left;
  }
  .logo-wrap {
    float: left;
    margin: 8px 5px;
    height: 44px;
    width: 125px;
    line-height: 60px;
    background-image: url(../../assets/logo.png);
    background-size:100%;
    background-repeat: no-repeat;
  }
  .user-wrap {
    float: right;
    height: 60px;
    width: 200px;
    line-height: 60px;
  }
  .user-info {
    padding-right: 20px;
    float: right;
    height: 60px;
    width: 70px;
    line-height: 60px;
  }
  .header-image {
    float: right;
    background-color: azure;
    margin: 10px;
    height: 40px;
    width: 40px;
    display: inline-block;
    border-radius: 40px;
    border-color: azure;
    overflow: hidden;
  }
  .header-image img {
    width: 40px;
    height: 40px;
  }
</style>



