<template>
  <div id="stage">
    <Header></Header>
    <div id="container" v-bind:style="{height:height + 'px'}">
      <keep-alive>
        <router-view class="child-view" :containerHeight="height" />
      </keep-alive>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from './layout/Header'
import Footer from './layout/Footer'

export default {
  data () {
    return {
      fullHeight: document.documentElement.clientHeight
    }
  },
  components: {
    Header, Footer
  },
  mounted () {
    const that = this
    window.onresize = () => {
      return (() => {
        window.fullHeight = document.documentElement.clientHeight
        that.fullHeight = window.fullHeight
      })()
    }
  },
  computed: {
    height: function () {
      return this.fullHeight - 120
    }
  },
  methods: {
  }
}
</script>
<style>
  #stage {
    width: 100%;
    height: inherit;
    background: #eee;
    overflow: hidden;
  }
  #container {
    margin-top: 60px;
    width: inherit;
    padding: 20px 0 20px;
    overflow: hidden;
    position: fixed;
  }
  .child-view {
    width: 100%;
    height: inherit;
    overflow-y: scroll;
    overflow-x: hidden;
  }
</style>



